// _footer.scss
.footer {
  background-image: url("data:image/png;base64,");
  background-color: #131313;
  color: #FFF;
  padding-top: 1.5rem;
}

.footer .menu a {
  color: #FFF;
  padding: 0.4rem 1rem 0.4rem 0;
  border-bottom: 1px dashed #1f1f1f;
}

.footer .menu li {
  padding: 0;
}

.footer .menu a:before {
  content: "//";
  color: #ff0;
  margin-right: 5px;
}

/* Medium and up */

@media screen and (min-width: 40em) {
  .footer .menu li {
    padding-right: 0.9375rem;
    padding-left: 0.9375rem;
  }
  .footer .menu a {
    border: none;
  }
}

.copy p {
  color: #4c4b4b;
}

ul.menu-parceiros {
  list-style-type: none;
  display: flex;
  li {
    margin-left: 10px;
    &.sinapro-pr {
      a {
        width: 67px;
        height: 30px;
        display: block;
        background: url("../img/logo-sinapro.png") no-repeat 0 top;
        text-indent: -9999px;
        cursor: pointer;
        background-size: cover;
      }
      &:hover {
        a {
          background: url("../img/logo-sinapro.png") no-repeat 0 bottom;
          background-size: cover;
        }
      }
    }
    &.cenp {
      a {
        width: 80px;
        height: 30px;
        display: block;
        background: url("../img/logo-cenp.png") no-repeat 0 top;
        text-indent: -9999px;
        cursor: pointer;
        background-size: cover;
      }
      &:hover {
        a {
          background: url("../img/logo-cenp.png") no-repeat 0 bottom;
          background-size: cover;
        }
      }
    }
  }
}