  padding-bottom: 3rem; }

.widget-sidebar #searchform input[type='text'] {
  width: 100%;
  height: 40px;
  line-height: 1.6rem !important;
  padding: 10px 20px;
  background: #FFF;
  border: 1px solid #EBEBEB;
  font-size: 0.9rem;
  margin: 0;
  text-shadow: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-shadow: inset 2px 2px 4px #EBEBEB;
  box-shadow: inset 2px 2px 4px #EBEBEB; }

.button.submit-button {
  font-family: FontAwesome;
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 1rem;
  padding: 0.85em 1em;
  -webkit-appearance: none;
  border: 1px solid transparent;
  border-radius: 0;
  transition: background-color 0.25s ease-out, color 0.25s ease-out;
  font-size: 0.9rem;
  line-height: 1;
  text-align: center;
  cursor: pointer;
  background: #161616;
  color: #FFF;
  margin: auto; }

/* Medium and up */
@media screen and (min-width: 40em) {
  .blog-wrapper {
    padding: 2rem 1.5rem; }
  .main-blog {
    padding-top: 5rem;
    padding-bottom: 5rem; }
  .inner-column-post .post-title {
    font-weight: bold;
    font-size: 2.5rem; } }

/* Large and up */
@media screen and (min-width: 64em) {
  .blog-wrapper {
    padding: 2rem 3rem; }
  .main-blog {
    padding-top: 5rem;
    padding-bottom: 5rem; }
  .inner-column-post {
    padding: 1.5rem; }
  .inner-column-post:first-child {
    padding-top: 0; }
  .inner-column-post .stat {
    margin-bottom: 0;
    font-size: 2.5rem; } }

ul.pagination {
  margin-top: 1rem; }

.pagination a {
  background: #FFF;
  color: #131313; }

.pagination a:hover,
.pagination button:hover {
  color: #131313;
  background: #FF0; }

.pagination .current,
.pagination .current:hover {
  color: #131313;
  background: #e6e6e6; }

.widget-sidebar ul {
  padding: 0 10px; }

.widget-sidebar li {
  padding: 5px 10px 5px 0;
  border-bottom: 1px dashed #ebebeb;
  list-style: square;
  display: list-item; }

.widget-sidebar li:last-child {
  border-bottom: none; }

.widget-sidebar a {
  color: #6b6b6b; }

.widget-sidebar a:hover {
  color: #131313; }

/*TAG CLOUD*/
.post-tags p {
  margin-bottom: 0; }

.post-tags a,
.tagcloud a {
  background: #FF0;
  color: #131313;
  padding: 2px; }

/*SINGLE POST*/
.single-post .inner-column-post {
  padding-top: 0;
  border: none; }

.inner-column-post h1:before {
  display: none; }

.entry-meta {
  color: #6b6b6b;
  margin-bottom: 1rem;
  margin-top: -0.5rem;
  border-bottom: 1px dashed #ebebeb;
  border-top: 1px dashed #ebebeb;
  padding: 0.2rem 0.5rem; }

.comments-area {
  color: #6B6B6B;
  margin-bottom: 1rem;
  margin-top: 0;
  /*border-top: 1px dashed #ebebeb;*/
  padding: 0.5rem;
  list-style: none; }

.single-post .comments-area {
  margin-bottom: 0; }

#respond {
  padding: 1rem 0 0;
  margin-top: 1rem;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  /*border-top: 1px dashed #ebebeb;*/ }

#commentform p {
  color: #4c4b4b; }

#commentform input[type='text'],
#commentform textarea {
  width: 100%;
  height: 50px;
  line-height: 1.6rem !important;
  padding: 10px 20px;
  background: #FFF;
  border: 1px solid #E6E6E6;
  font-size: 20px;
  margin: 20px 0 10px;
  text-shadow: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-shadow: inset 2px 2px 4px #EBEBEB;
  box-shadow: inset 2px 2px 4px #EBEBEB; }

#commentform textarea {
  height: 200px; }

#commentform #submit {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 1rem;
  padding: 0.85em 1em;
  -webkit-appearance: none;
  border: 1px solid transparent;
  border-radius: 0;
  transition: background-color 0.25s ease-out, color 0.25s ease-out;
  font-size: 0.9rem;
  line-height: 1;
  text-align: center;
  cursor: pointer;
  background: #161616;
  color: #FFF;
  margin: auto;
  width: 150px; }

.comment-reply-title {
  color: #131313; }

.commentlist {
  padding: 15px; }

.comment-body {
  background-color: #f9f9f9;
  border-bottom: 1px dashed #cfcfcf;
  overflow: hidden;
  padding: 15px; }

.comment-body a {
  color: #131313; }

.odd {
  padding: 15px 0 15px 30px; }

.odd .comment-body {
  background: #f1f1f1;
  overflow: hidden;
  padding: 15px; }

.vcard {
  font-family: monospace; }

.vcard img {
  width: 32px;
  float: left;
  margin-right: 10px; }

.odd .fn {
  color: #131313; }

.odd .says {
  color: #131313; }

ul.children {
  padding: 15px 0; }

/*AUTOR*/
.author-box {
  background: #131313;
  padding: 2rem;
  color: #FFF;
  margin-bottom: 5rem; }

.page-template-template-contato .main [type='text'],
.page-template-template-contato .main [type='email'],
.page-template-template-contato .main [type='tel'] {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding: 10px 20px;
  background: #FFF;
  border: 1px solid #E6E6E6;
  font-size: 20px;
  margin: 20px 0 10px;
  text-shadow: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-shadow: inset 2px 2px 4px #EBEBEB;
  box-shadow: inset 2px 2px 4px #EBEBEB; }

.page-template-template-contato .main textarea {
  width: 100%;
  height: 150px;
  line-height: 1.6rem;
  padding: 10px 20px;
  background: #FFF;
  border: 1px solid #E6E6E6;
  font-size: 20px;
  margin: 20px 0 10px;
  text-shadow: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-shadow: inset 2px 2px 4px #EBEBEB;
  box-shadow: inset 2px 2px 4px #EBEBEB; }

/*NEWSLETTER FORM*/
.newsletter-box .wpcf7-form-control-wrap {
  width: 100%; }

.newsletter-box .wpcf7-email {
  height: 42px; }

.newsletter-box .input-group-button {
  position: relative; }

div.wpcf7 img.ajax-loader {
  position: absolute;
  top: 0;
  left: 0;
  background: #131313;
  padding: 12px 33px; }

div.wpcf7-response-output {
  margin: 1em 0.5em;
  padding: 0.2em 1em; }

div.wpcf7-mail-sent-ok {
  border: 2px solid #398f14;
  background: #398F14; }

div.wpcf7-mail-sent-ng {
  border: 2px solid #ff0000;
  background: #F00; }

div.wpcf7-spam-blocked {
  border: 2px solid #ffa500;
  background: #FFA500; }

div.wpcf7-validation-errors {
  border: 2px solid #FF0;
  background: #FF0;
  color: #131313; }

span.wpcf7-not-valid-tip {
  text-align: left; }

