// _header.scss
.header {
  background: #000;
}

.main-header-content {
  position: absolute;
  z-index: 111;
  width: 100%;
}

.off-canvas {
  background-image: url("data:image/png;base64,");
  background-color: rgba(19, 19, 19, 1);
  z-index: 2;
}

.close-button,
.close-button:focus,
.close-button:hover {
  color: #FFF;
}

.logo-home {
  margin: 1rem 0;
}

.info-header {
  padding: 1rem 1rem 0;
}

.info-header p {
  color: #FFF;
  margin-bottom: 0;
}

.info-header i {
  color: #FF0;
  margin-left: 1rem;
}

.info-header hr {
  margin: 1rem 0 0;
}